html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.scroll-body {
  padding-top: 106px;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h4 {
  font-size: 3rem;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #4cb8b3;
}
@media (min-width: 768px) {
  .header.fijo {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    box-shadow: 0.1rem 0.1rem 0.1rem #111;
  }
}
.header h1 {
  text-align: center;
  color: #fff;
  margin: 0rem 0rem;
}
@media (min-width: 992px) {
  .header h1 {
    text-align: left;
    color: #fff;
    margin: 0;
  }
}
@media (min-width: 768px) {
  .header h1 {
    text-align: center;
    color: #fff;
    margin: 2rem 0rem;
  }
}
@media (min-width: 360px) {
  .header h1 {
    text-align: center;
    color: #fff;
    margin: 1rem 0rem;
  }
}
@media (min-width: 768px) {
  .header .container-nav {
    padding: 2rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
}
@media (min-width: 360px) {
  .header .container-nav {
    padding: 2rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
}
@media (min-width: 992px) {
  .header .container-nav {
    padding: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}

.navegacion a {
  color: #fff;
  padding: 20px;
}
.navegacion a:hover {
  color: #fef46d;
  font-size: 20px;
  transition: all 0.5s;
}
@media (min-width: 360px) {
  .navegacion a {
    color: #fff;
    padding: 20px;
  }
  .navegacion a:hover {
    color: #fef46d;
    font-size: 20px;
    transition: all 0.5s;
  }
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
  overflow: hidden;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.overlay-video {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(188, 0, 255);
  background: linear-gradient(270deg, rgba(188, 0, 255, 0.5) 0%, rgba(255, 0, 164, 0.3) 100%);
}
.overlay-video .contenido-video {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.overlay-video .contenido-video h2 {
  color: #fff;
  font-size: 7rem;
  margin: 0;
}
.overlay-video .contenido-video p {
  color: #fff;
  background-color: #f53756;
  padding: 1rem 2rem;
  width: 450px;
  text-align: center;
  font-weight: 700;
  margin: 0;
}

.festival {
  height: 100%;
}

.grid-festival {
  display: flex;
  flex-direction: column;
  padding-top: 5rem;
}
@media (min-width: 992px) {
  .grid-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.grid-festival .texto-festival {
  margin-top: 4rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .grid-festival .texto-festival {
    margin-top: 0;
    text-align: center;
  }
  .grid-festival .texto-festival .festival {
    height: 1000px;
  }
}
@media (min-width: 992px) {
  .grid-festival .texto-festival {
    text-align: left;
  }
  .grid-festival .texto-festival .festival {
    height: 1000px;
  }
}
.grid-festival .texto-festival .p_fecha {
  color: #f53756;
  font-weight: 600;
}

.lineup {
  height: 2900px;
  background-color: #f53756;
}
@media (min-width: 768px) {
  .lineup {
    height: 1700px;
  }
}
.lineup h3 {
  text-align: center;
  padding-top: 50px;
  font-size: 3rem;
  color: #fff;
}

@media (min-width: 768px) {
  .contenedor-escenarios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    padding-bottom: 0;
  }
}

.escenario .nombre_escenario {
  text-align: center;
  padding: 2rem;
}
.escenario .logo {
  width: 15rem;
  height: 10rem;
}

.p_dia {
  text-align: center;
  color: #fff;
  margin: 5rem 0;
}

.bg-amarillo {
  background-color: #fef46d;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #fdee21;
}

.bg-verde {
  background-color: #4cb8b3;
}
.bg-verde li:nth-child(odd) {
  background-color: #3b9691;
}

.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
  padding-left: 2px;
}

.grid-imagenes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .grid-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 992px) {
  .grid-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.galeria {
  padding: 4rem 0;
  height: 100%;
}
@media (min-width: 768px) {
  .galeria {
    height: 100%;
  }
}
.galeria h2 {
  text-align: center;
}
.galeria .containerg-galeria {
  height: 100%;
}
.galeria .img3 {
  height: 100%;
}
.galeria .grid-imagenes {
  padding-top: 2rem;
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: auto;
}
.overlay picture {
  max-width: 80rem;
  display: block;
}
.overlay .btn-cerrar {
  color: white;
  font-weight: 700;
  border: 2px solid white;
  display: grid;
  place-items: center;
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
  font-size: 1.3;
}
.overlay .btn-cerrar:hover {
  cursor: pointer;
  color: black;
  transition: all 0.5s;
  background-color: white;
}

.boletos {
  height: 100%;
  padding: 4rem 0;
  background-color: #4cb8b3;
}
.boletos h2 {
  color: #fff;
  text-align: center;
  font-size: 3rem;
}

.container-boletos {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 7rem;
}
@media (min-width: 768px) {
  .container-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 7rem;
  }
}
.container-boletos .pase-caracteristicas {
  background-color: #fff;
}
.container-boletos .pase-caracteristicas p {
  font-size: 16px;
  margin: 0;
  padding: 10px;
  border-bottom: 1px solid #e1e1e1;
}
.container-boletos .pase-titulo {
  background-color: #f53756;
  display: grid;
  place-items: center;
  height: 40px;
}
.container-boletos .pase-titulo h3 {
  text-align: center;
  color: #fff;
  margin: 0;
}
.container-boletos .precio {
  display: grid;
  place-items: center;
  background-color: #752f97;
  height: 50px;
}
.container-boletos .precio h3 {
  text-align: center;
  color: #fff;
  margin: 0;
}

.pase-dia {
  text-align: center;
  font-size: 28px;
  color: #fff;
  font-weight: 500;
}

.piepagina {
  padding: 1rem 0;
  background-color: #f53756;
}

.p-piepag {
  color: #fff;
  text-align: center;
  width: 100%;
  font-weight: 400;
  font-size: 18px;
  margin: 0;
}